'use client';
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Dna, Wand2 } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { generateTimetableAction, optimizeTimetableAction } from '@/app/actions';

type FormValues = {
  courseSchedules: string;
  facultyAvailability: string;
  studentPreferences: string;
};

export default function AiTimetableForm() {
  const [isLoading, setIsLoading] = useState(false);
  const [isOptimizing, setIsOptimizing] = useState(false);
  const [generatedDraft, setGeneratedDraft] = useState<string | null>(null);
  const [optimizedResult, setOptimizedResult] = useState<{ timetable: string; summary: string } | null>(null);
  const { toast } = useToast();
  const { register, handleSubmit, getValues } = useForm<FormValues>();

  const onGenerate = async (data: FormValues) => {
    setIsLoading(true);
    setGeneratedDraft(null);
    setOptimizedResult(null);
    try {
      const result = await generateTimetableAction(data);
      setGeneratedDraft(result.timetableDraft);
      toast({ title: 'Success', description: 'Timetable draft generated!' });
    } catch (error) {
      toast({ variant: 'destructive', title: 'Error', description: (error as Error).message });
    } finally {
      setIsLoading(false);
    }
  };

  const onOptimize = async () => {
    if (!generatedDraft) return;
    setIsOptimizing(true);
    setOptimizedResult(null);
    try {
      const formValues = getValues();
      const inputs = {
        timetable: generatedDraft,
        facultyAvailability: formValues.facultyAvailability,
        studentCoursePreferences: formValues.studentPreferences,
      };
      const result = await optimizeTimetableAction(inputs);
      setOptimizedResult({ timetable: result.optimizedTimetable, summary: result.summary });
      toast({ title: 'Success', description: 'Timetable optimized!' });
    } catch (error) {
        toast({ variant: 'destructive', title: 'Error', description: (error as Error).message });
    } finally {
      setIsOptimizing(false);
    }
  };

  return (
    <div className="grid gap-6 lg:grid-cols-2">
        <Card>
            <CardHeader>
                <CardTitle>1. Input Constraints</CardTitle>
                <CardDescription>Provide the necessary details for timetable generation.</CardDescription>
            </CardHeader>
            <CardContent>
                <form onSubmit={handleSubmit(onGenerate)} className="flex flex-col gap-4">
                    <div>
                        <Label htmlFor="courseSchedules">Course Schedules</Label>
                        <Textarea id="courseSchedules" placeholder="e.g., 'CS101: Mon 10-11:30, Wed 2-3:30'" {...register('courseSchedules')} rows={4} />
                    </div>
                    <div>
                        <Label htmlFor="facultyAvailability">Faculty Availability</Label>
                        <Textarea id="facultyAvailability" placeholder="e.g., 'Dr. Smith: Mon-Wed 9-5, unavailable Thu'" {...register('facultyAvailability')} rows={4} />
                    </div>
                    <div>
                        <Label htmlFor="studentPreferences">Student Preferences</Label>
                        <Textarea id="studentPreferences" placeholder="e.g., 'Group A prefers morning classes'" {...register('studentPreferences')} rows={4} />
                    </div>
                    <Button type="submit" disabled={isLoading} className="w-full">
                        {isLoading ? <Dna className="mr-2 h-4 w-4 animate-spin" /> : <Wand2 className="mr-2 h-4 w-4" />}
                        Generate Draft
                    </Button>
                </form>
            </CardContent>
        </Card>

        <div className="flex flex-col gap-6">
            {generatedDraft && (
                <Card className="flex-1 animate-in fade-in duration-500">
                    <CardHeader>
                        <CardTitle>2. Generated Draft</CardTitle>
                        <CardDescription>Review the AI-generated draft and optimize if needed.</CardDescription>
                    </CardHeader>
                    <CardContent className="flex flex-col gap-4">
                        <Textarea value={generatedDraft} readOnly rows={10} className="bg-muted/50 font-mono text-xs" />
                        <Button onClick={onOptimize} disabled={isOptimizing} variant="secondary">
                           {isOptimizing ? <Dna className="mr-2 h-4 w-4 animate-spin" /> : <Wand2 className="mr-2 h-4 w-4" />}
                            Optimize this Timetable
                        </Button>
                    </CardContent>
                </Card>
            )}

            {optimizedResult && (
                <Card className="flex-1 animate-in fade-in duration-500">
                    <CardHeader>
                        <CardTitle>3. Optimized Result</CardTitle>
                        <CardDescription>Final timetable and summary of optimizations.</CardDescription>
                    </CardHeader>
                    <CardContent className="flex flex-col gap-4">
                        <Label>Optimized Timetable</Label>
                        <Textarea value={optimizedResult.timetable} readOnly rows={8} className="bg-muted/50 font-mono text-xs" />
                        <Label>Optimization Summary</Label>
                        <Textarea value={optimizedResult.summary} readOnly rows={4} className="bg-muted/50" />
                    </CardContent>
                </Card>
            )}
        </div>
    </div>
  );
}
