'use server';

import { generateTimetable, type GenerateTimetableInput, type GenerateTimetableOutput } from '@/ai/flows/generate-timetable';
import { optimizeTimetable, type OptimizeTimetableInput, type OptimizeTimetableOutput } from '@/ai/flows/optimize-timetable';

export async function generateTimetableAction(input: GenerateTimetableInput): Promise<GenerateTimetableOutput> {
    try {
        const result = await generateTimetable(input);
        return result;
    } catch (error) {
        console.error("Error generating timetable:", error);
        throw new Error("Failed to generate timetable with AI.");
    }
}

export async function optimizeTimetableAction(input: OptimizeTimetableInput): Promise<OptimizeTimetableOutput> {
    try {
        const result = await optimizeTimetable(input);
        return result;
    } catch (error) {
        console.error("Error optimizing timetable:", error);
        throw new Error("Failed to optimize timetable with AI.");
    }
}
